@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);
.navigation{
    width: 100%;
    height: 45px;
    position: fixed;
    background: #ffffff;
    top: 0;
    z-index: 10000;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
}

.navigation a{
    text-decoration: none;
    color: #000000;
    font-weight: bold;
    margin-right: 35px;
    font-size: 18px;
    transition: all 0.3s;
}

.navigation a:hover{
    color: #cccccc;
}
.about__card{
    width: 450px;
    padding: 25px 35px;
    border-radius: 8px;
    background: #ffffff;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    -webkit-transform: translateY(-70px);
            transform: translateY(-70px);
}

.about__title{
    margin-top: 0;
    text-align: center;
}

.about__text{
    font-size: 19px;
    text-align: center;
}
.movies {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 100px 50px 0;
    width: 80%;
  }

  .movies a{
    width: 45%;
    text-decoration: none;
  }
  
  .movies .movie {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    background-color: white;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    margin-bottom: 70px;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  }
  
  .movie img {
    position: relative;
    max-width: 150px;
    width: 100%;
    top: -50px;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    transition: all 0.4s;
  }

  .movie img:hover {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
  }
  
  .movie .movie__title,
  .movie .movie__year {
    font-weight: 300;
    margin: 0;
  }
  
  .movie .movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
    font-weight: 600;
  }
  
  .movie .movie__genres {
    list-style: none;
    padding: 0;
    margin: 5px 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
  
  .movie__genres li,
  .movie .movie__year {
    font-size: 14px;
    margin-right: 10px;
  }
  
  .movie .movie__column {
    width: 80%;
  }
body {
    background-color: #f2f2f2;
    font-family: 'Montserrat', sans-serif;
  }

  * {
    box-sizing: border-box;
  }
  
  body {
    background-color: #eff3f7;
    margin: 0;
    padding: 0;
    height: 100%;
    padding-top: 45px;
    width: 100%;
  }

  #root{
    width: 100%;
  }
  
  html,
  body,
  #root,
  .container {
    min-height: 100%;
    height: -webkit-max-content;
    height: max-content;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .loader {
    width: 100%;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
.detail{
    margin-top: 25px;
    width: 38%;
    background: #ffffff;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    display: -webkit-flex;
    display: flex;
    color: #fff;
    height: 90vh;
    position: relative;
    border-radius: 8px;
}

.detail__poster{
    height: 95%;
    width: 75%;
    background-size: cover;
    background-position: center center;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    border-radius: 8px;
}

.detail__column{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    z-index: 10;
}
.detail__title{
    width: -webkit-max-content;
    width: max-content;
    background: rgba(0,0,0,0.5);
    padding: 7px;
    border-radius: 7px;
    border-bottom-left-radius: 0;
    margin: 0;
    font-weight: 600;
}
.detail__year{
    width: -webkit-max-content;
    width: max-content;
    background: rgba(0,0,0,0.5);
    padding: 7px;
    margin: 0;
}
.detail__genres{
    list-style: none;
    width: -webkit-max-content;
    width: max-content;
    background: rgba(0,0,0,0.5);
    padding: 7px;
    border-radius: 7px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    border-top-left-radius: 0;
    margin: 0;
}
.detail__summary{
    width: 100%;
    background: rgba(0,0,0,0.5);
    padding: 7px;
    border-radius: 7px;
    border-top-left-radius: 0;
    margin: 0;
}
